export default class Breadcrumbs extends React.PureComponent {
  renderResource() {
    return (
      <li>
        <Link to={this.props.resource.href} className={this.props.record && 'is-active'}>
          {this.props.resource.name}
        </Link>
      </li>
    )
  }

  render() {
    return (
      <nav className="breadcrumb" aria-label="breadcrumbs">
        <ul>
          {this.renderResource()}
        </ul>
      </nav>
    )
  }
}